<template>
  <div>
    <!-- <transition
        name="custom-classes-transition"
        mode="out-in"
        enter-active-class="animated fadeIn"
        leave-active-class="animated fadeOut">
      <div v-show="!$store.state.loaded" id="cover">
        <div class="spinner">
          <div class="double-bounce1"></div>
          <div class="double-bounce2"></div>
        </div>
      </div>
    </transition> -->
    <div id="app" v-bind:class="[isPortfolio ? 'body-portfolio' : 'body-no-portfolio']">
      <transition
        mode="out-in"
        enter-active-class="animated slideInLeft"
        leave-active-class="animated slideOutLeft">
        <nav v-if="!isPortfolio" class="navbar navbar-expand-lg navbar-dark bg-primary fixed-top" id="sideNav">
          <router-link class="navbar-brand" to="/">
            <span class="d-block d-lg-none">José Ignacio Palma</span>
            <span class="d-none d-lg-block">
              <img class="img-fluid img-profile rounded-circle mx-auto mb-2" src="img/profile.jpg" alt="">
            </span>
          </router-link>
          <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
          </button>
          <div class="collapse navbar-collapse" id="navbarSupportedContent">
            <ul class="navbar-nav">
              <li class="nav-item">
                <router-link class="nav-link" to="/">
                  <span v-if="isEn">About Me</span>
                  <span v-if="isEs">Sobre Mi</span>
                </router-link>
              </li>
              <li class="nav-item">
                <router-link class="nav-link" to="/experience">
                  <span v-if="isEn">Experience</span>
                  <span v-if="isEs">Experiencia</span>
                </router-link>
              </li>
              <li class="nav-item">
                <router-link class="nav-link" to="/education">
                  <span v-if="isEn">Education</span>
                  <span v-if="isEs">Educación</span>
                </router-link>
              </li>
              <li class="nav-item">
                <router-link class="nav-link" to="/skills">
                  <span v-if="isEn">Skills</span>
                  <span v-if="isEs">Habilidades</span>
                </router-link>
              </li>
              <li class="nav-item">
                <router-link class="nav-link" to="/interests">
                  <span v-if="isEn">Interests</span>
                  <span v-if="isEs">Intereses</span>
                </router-link>
              </li>
              <li class="nav-item">
                <router-link class="nav-link" to="/portfolio">
                  <span v-if="isEn">Portfolio</span>
                  <span v-if="isEs">Portafolio</span>
                </router-link>
              </li>
              <!-- <li class="nav-item">
                <div><a href="!#" class="nav-link">ES</a> / <a href="!#" class="nav-link">EN</a></div>
              </li> -->
            </ul>
          </div>
        </nav>
      </transition>
      <transition
        name="custom-classes-transition"
        mode="out-in"
        enter-active-class="animated slideInRight"
        leave-active-class="animated slideOutRight">
        <router-view/>
      </transition>
      <a class="hvr-sweep-to-right button-fixed" v-bind:class="[isPortfolio ? 'button-fixed-portfolio' : 'button-fixed-resume']" @click="changeLenguage">
        <span v-if="$store.state.language == 'en'">ES</span>
        <span v-if="$store.state.language == 'es'">EN</span>
      </a>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        currentRoute: window.location.pathname
      }
    },
    mounted() {
      document.querySelector("body").classList.remove("nav-active");
      document.title = this.$router.currentRoute.meta.title;
    },
    updated() {
      document.querySelector("body").classList.remove("nav-active");
      document.title = this.$router.currentRoute.meta.title;
      this.currentRoute = window.location.pathname;
    },
    methods: {
      changeLenguage: function() {
        if(this.$store.state.language == 'en')
          this.$store.state.language = 'es';
        else if(this.$store.state.language == 'es')
          this.$store.state.language = 'en';
      }
    },
    computed: {
      isPortfolio: function() {
        return this.currentRoute.includes("portfolio");
      },
      isEs: function() {
        return this.$store.state.language == 'es';
      },
      isEn: function() {
        return this.$store.state.language == 'en';
      }
    },
    watch:{
      $route (to, from){
        this.$store.state.loaded = false;
      }
    } 
  }
</script>
